import React, { Component } from 'react';
import {Link} from 'react-router-dom';


class Navbar extends Component {
    render() {
        return (
            <div>
                
                <nav>
 <ul className="nav-link">
     <Link to="/about" style={{color: "white", textDecoration:'none'}} activeStyle={{color: 'blue'}}>
     <li>About Us</li>
     </Link>
     <Link to="/gallary" style={{color: "white", textDecoration:'none'}}>
     <li>Gallary</li>
     </Link>
     <Link to="/contact" style={{color: "white", textDecoration:'none'}}>
     <li>Contact</li>
     </Link>
     <Link to="/contact" style={{color: "white", textDecoration:'none'}}>
     <li>Blog</li></Link>

     
     
     </ul>

                </nav>
               
            </div>
        )
    }
}
export default Navbar
