import React, { Component } from 'react';
import { PieChart } from 'react-minimal-pie-chart';


class Fskill extends Component {

    constructor(){
    super();
    this.state=
    {
        dataMock: [
            { title: 'Requirement Analysis(10%)', value: 10, color: '#E38627' },
            { title: 'Client Communication(15%)', value: 15, color: '#C13C37' },
            { title: 'Cost Estimation(10%)', value: 10, color: '#6A2135' },
            { title: 'Team Management(20%)', value: 20, color: '#E38627' },
            { title: 'Project Documentation(15%)', value: 15, color: '#6A2135' },
            { title: 'Vendor Management(10%)', value: 10, color: '#C13C37' },
            { title: 'Design and coding(20%)', value: 20, color: '#6A2135' }
          ],

          defaultLabelStyle: {fontSize: '.13em', fontWeight:"bold"}
         
    }

    



    }

    

    render() {
        return (

            <div>
            <PieChart data={this.state.dataMock} label={({ dataEntry }) => dataEntry.title} labelStyle={this.state.defaultLabelStyle} radius={23} labelPosition={105}  lineWidth={30}
      rounded/>

            </div>
        )
    }

}
export default Fskill
